import { h, Component } from 'preact';
import { useEffect } from 'preact/hooks';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer } from 'preact-i18n';
import { route } from 'preact-router';
import Markdown from 'preact-markdown';
import linkstate from 'linkstate';

import RaceTracking from '../../components/public/raceTracking.js';
import RaceLiveResults from '../../components/races/liveResults';

import mu from '../../lib/musher-util';
import style from './style.css';

const MARGIN_TOP = mu.marginTop(false);
const MARGIN_TOP_BACK = mu.marginTopBack(false);
const MARGIN_BOTTOM = mu.marginBottom();
const SUB_MENU_MARGIN_TOP = mu.subMenuMarginTop();

const initialState = {};

const RELOAD_INTERVAL_IN_SEC = 60;

@observer
class TvTop10Progress extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
        this.updateTimer = {};
    }

    loadLiveRaceResults = async (props = this.props, { teamIds, focusOnMushers, bibs, classid }) => {
        const { raceStore, userStore } = props.stores;
        const { race } = raceStore;
        const { historyPointIndex, replayHistory, timerRunning } = this.state;
        const { showDrawerReplay } = this.props;
        const classInfo = raceStore.getClassById(classid) || {};
        const { isAdmin } = userStore;

        await raceStore.getRaceTrackerData(race.raceTrackerRace, {
            history: (replayHistory || showDrawerReplay),
            bibs,
            classid,
            startDate: classInfo.startDate,
            endDate: classInfo.endDate,
            isAdmin,
        });
        raceStore.getRaceTrackerActiveTeams(teamIds, focusOnMushers, bibs, historyPointIndex);

        // Set reload timer
        if (timerRunning) {
            clearTimeout(this.updateTimer.reload);
            this.updateTimer.reload = setTimeout(() => {
                console.log('loadLiveRaceResults.reloading...');
                const reloadLastEpoch = Math.floor(new Date().getTime() / 1000);
                this.setState({ reloadLastEpoch });
                // this.loadRace(props);
                this.loadLiveRaceResults(this.props, { teamIds, focusOnMushers, bibs, classid });
            }, RELOAD_INTERVAL_IN_SEC * 1000);
        }
    }


    loadRaceResults = async (props = this.props, teamIds, focusOnMushers) => {
        const { raceStore } = props.stores;
        const { raceid } = props;

        await raceStore.loadWorkoutSummary(teamIds, raceid);
        raceStore.getWorkoutSummaryActiveTeams(teamIds, focusOnMushers);
    }

    loadRace = async (props = this.props) => {
        try {
            this.setState({ isLoading: true });
            const { raceStore, appState } = props.stores;
            const { raceid, classid } = props;
            await raceStore.load(raceid);
            const { race } = raceStore;
            const { focusOnMushers } = appState;

            const { selectedClassIds = [] } = appState;
            const selectedClassId = classid || selectedClassIds[0];

            if (classid) {
                appState.toggleSelectedClassId(classid, true);
            }
            const allTeamIds = race.classes
                .filter(currentClass => currentClass.id === selectedClassId)
                .filter(currentClass => currentClass.contestants && currentClass.contestants.length > 0)
                .map((currentClass) => {
                    return currentClass.contestants.filter(musher => musher.team > 0).map(musher => musher.team);
                })
                .flat();

            const bibs = race.classes
                .filter(currentClass => currentClass.id === selectedClassId)
                .filter(currentClass => currentClass.contestants && currentClass.contestants.length > 0)
                .map((currentClass) => {
                    return currentClass.contestants.filter(musher => musher.bib > 0).map(musher => musher.bib);
                })
                .flat();

            if (allTeamIds.length > 0) {
                await raceStore.loadTeams(allTeamIds.join(','));
            }
            if (race.liveRace > 0) {
                await this.loadLiveRaceResults(this.props, { teamIds: allTeamIds, focusOnMushers, bibs, classid: selectedClassId });
            } else {
                await this.loadRaceResults(this.props, allTeamIds, focusOnMushers);
            }
            raceStore.getAllClassesSortedBy();

            this.setState({ isLoading: false });
		} catch (err) {
			console.log(err);
		}
    }

	componentWillUnmount() {
		clearTimeout(this.updateTimer.reload);
		clearTimeout(this.updateTimer.tick);
	}

	componentDidMount() {
		this.loadRace();
	}

    componentWillReceiveProps(nextProps) {
        if (this.props.classid !== nextProps.classid) {
            this.loadRace(nextProps);
        }
	}

    toggleFocusOnMusher = (e) => {
		const musherId = e.target.parentElement.dataset.musherid || e.target.dataset.musherid;
		const { appState, raceStore } = this.props.stores;
		appState.toggleFocusOnMusher(musherId);

		const { focusOnMushers } = appState;

        const { race } = raceStore;
        if (race.liveRace > 0) {
            raceStore.getRaceTrackerActiveTeams(null, focusOnMushers);
        } else {
            raceStore.getWorkoutSummaryActiveTeams(null, focusOnMushers);
        }
	}

    toggleBib = () => {
        const { bib } = this.state;
        const { appState, raceStore } = this.props.stores;
        const musher = raceStore.getMusherByBibId(bib);
		appState.toggleFocusOnMusher(musher.id);

        const { focusOnMushers } = appState;
        const { race } = raceStore;
        if (race.liveRace > 0) {
            raceStore.getRaceTrackerActiveTeams(null, focusOnMushers);
        } else {
            raceStore.getWorkoutSummaryActiveTeams(null, focusOnMushers);
        }
    }

    checkForEnter = (e) => {
        if (e.key === 'Enter') {
            this.toggleBib();
        }
    }

    toggleShowDrawerReplay = () => {
        const { showDrawerReplay } = this.state;
        this.setState({
            showDrawerReplay: !showDrawerReplay,
        });
    }

    back = () => {
        const { history } = this.props.stores;
        history.goBack();
    }


    render() {
        const { showDrawerReplay } = this.state;
        const { userStore, raceStore, appState } = this.props.stores;
        const { user = {}, teams = [], isAdmin } = userStore;

        const { raceid, classid, path, position = 'right', limit = 10 } = this.props;
        const { focusOnMushers = [] } = appState;

        const { race } = raceStore;
        const selectedClass = raceStore.getClassById(classid);

        return (<>
            <div class={`fixed-top ${style.hoverElement}`} style='height: 100px;'>
                <div class={`${style.hoverelementonly}`}>
                    <div class={`row`}>
                        {race.classes && race.classes.sort((a, b) => a.distance - b.distance).map((currentClass, idx) => <>
                            <div class='col text-center'>
                                <a
                                    class={`btn btn-block ${currentClass.id === classid ? 'btn-success' : 'btn-outline-success'} text-nowrap`}
                                    style='text-overflow: ellipsis; overflow: hidden;'
                                    data-id={currentClass.id}
                                    href={`/tv/top10progress/${race.id}/${currentClass.id}/${position}`}
                                >
                                    {currentClass.title}
                                </a>


                                <a
                                    class={`btn btn-link text-nowrap text-center`}
                                    style='text-overflow: ellipsis; overflow: hidden;'
                                    data-id={currentClass.id}
                                    href={`/tv/top10progress/${race.id}/${currentClass.id}/center`}
                                >
                                    center
                                </a>
                                <a
                                    class={`btn btn-link text-nowrap ${position === 'left' ? 'float-right' : 'float-left'}`}
                                    style='text-overflow: ellipsis; overflow: hidden;'
                                    data-id={currentClass.id}
                                    href={`/tv/top10progress/${race.id}/${currentClass.id}/${position === 'left' ? 'right' : 'left'}`}
                                >
                                    {position === 'left' ? <>
                                        right <i class='fas fa-angle-right' />
                                    </> : <>
                                        <i class='fas fa-angle-left' /> left
                                    </>}
                                </a>
                            </div>
                        </>)}
                    </div>
                </div>
            </div>

			<div class='container-fluid' style={`margin-bottom: ${MARGIN_BOTTOM}; margin-top: ${MARGIN_TOP};`}>
                <div class='row'>
                    <div class={position === 'center' ? 'offset-1 col-10' : (position === 'left' ? 'col-4 ml-5' : 'col-4 offset-8 mr-5')}>
                        <div
                            class='px-4 py-2 overflow-hidden'
                            style='
                                background-color: #FB4114; 
                                color: #ffffff; 
                                border-radius: 10px 10px 0 0; 
                                font-size: 1.8em;
                            '
                        >
                            <span class='float-right font-weight-light'>{selectedClass.title}</span>
                            Race standing
                        </div>
                        <div
                            class='mr-5 p-0 position-relative w-100'
                            style='
                                background: rgba(0, 0, 0, 0.3);
                                background-color: #1C1F4C;
                                background-image: url("https://themusher.app/assets/tv-single-background.jpg");
                                background-size: cover !important;
                                background-position: 0 0;    
                            '
                        >
                            {selectedClass && selectedClass.id && <RaceLiveResults
                                stores={this.props.stores}
                                race={race}
                                currentClass={selectedClass}
                                key={`raceclass-${selectedClass.id}`}
                                onClickMusher={this.toggleDrawerMusher}
                                onClickCheckpoint={this.toggleDrawerCheckpoint}
                                // selectedCheckpointId={selectedCheckpointId}
                                limit={limit}
                                tableClass={''}
                                tdClass={''}
                                tdStyle={'color: #ffffff; font-size: 1.5em;'}
                                thStyle={'color: #ffffff; font-size: 1.5em;'}
                                progressStyle={'height: 30px;'}
                                progressbarStyle={'font-size: 1.5em;'}
                            />}
                        </div>
                        <div
                            class='px-4 py-2 overflow-hidden text-center'
                            style='
                                background-color: #FB4114; 
                                color: #ffffff; 
                                border-radius: 0 0 10px 10px;
                                font-size: 1.4em;
                            '
                        >
                            <nobr class='mr-4'>QPaws app</nobr>
                                            
                            <img src='https://themusher.app/assets/appstore-download.png' height='40' class='ml-5 mr-1' />
                            <img src='https://themusher.app/assets/playstore-download.png' height='40' class='mx-1' />
                        </div>
                    </div>
                </div>
            </div>
        </>);
    }
}

export default TvTop10Progress;
